body {
  margin: 0;
  padding: 0;
  position: relative;
  min-height: 100vh;
  background: linear-gradient(to bottom right, #e95420, #6c0277);
}

body,
html {
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.clear{
  clear: both;
}

.navbar{
  display: flex;
  background: #FF9000;
  height: 80px;
  color: rgb(255, 239, 210);
  filter: drop-shadow(1px 1px 2px #252525);
}

.logo_navbar{
  position: relative;
  height: 85%;
  margin: 1%;
  margin-top: 0.5%;
  display: inline-block;
  margin-left: 20px;
}
.logo_main{
  position: relative;
  height: 55vh;
  margin-top: -45%;
  display: inline-block;
  filter: drop-shadow(1px 1px 5px #252525);
}
.name_navbar{
  position: relative;
  margin: 10%;
  align-self: center  ;
  margin-top: -6%;
  font-family: "Google Sans",Roboto,Arial,sans-serif;
  font-size: 3vh;
  font-weight: 500;
  color: #3c4043;
  margin-left: 12%;
}

.container {
  position: absolute;
  width: 950px;
  height: 550px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -45%);
  border: 50px solid rgb(40, 36, 36);
  border-radius: 30px;
}

.rectangle {
  width: 100%;
  height: 100%;
  background-color: rgb(40, 36, 36);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.button1 {
  padding: 270px 45px;
  margin: 10px;
  background-color: #72097e;
  color: white;
  border: none;
  border-radius: 20px;
  position: absolute;
  left: 9%;
  transform: translateX(-50%);
  cursor: pointer;
  font-size: 40px;
}

.button2 {
  padding: 270px 85px;
  margin: 10px;
  background-color: #fb8218;
  color: white;
  border: none;
  border-radius: 20px;
  position: absolute;
  left: 44.1%;
  transform: translateX(-50%);
  cursor: pointer;
  font-size: 40px;
  
}

.button3 {
  padding: 270px 78px;
  margin: 10px;
  background-color: #0e78c9;
  color: white;
  border: none;
  border-radius: 20px;
  position: absolute;
  left: 84%;
  transform: translateX(-50%);
  cursor: pointer;
  font-size: 40px;
}

.dot {
  position: absolute;
  top: -45px;
  left: -30px;
  width: 20px;
  height: 20px;
  background-color: red;
  border-radius: 50%;
}

.dot2 {
  position: absolute;
  top: -45px;
  left: -5px;
  width: 20px;
  height: 20px;
  background-color: rgb(248, 225, 14);
  border-radius: 50%;
}

.dot3 {
  position: absolute;
  top: -45px;
  left: 20px;
  width: 20px;
  height: 20px;
  background-color: rgb(45, 202, 25);
  border-radius: 50%;
}

.arrow-button {
  width: 85px;
  height: 85px;
  cursor: pointer;
}
